body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
.not-match {
    font-size: 38px;
    color: #DDDDDD;
    font-style: italic;
    font-family: serif;
    text-align: center;
    margin-top: 200px;
    font-weight: bold;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.indent {
    text-indent: 2em;
}
p{
    color: rgba(0,0,0,.87);
    font-size: 15px;
    -webkit-font-smoothing: subpixel-antialiased;
}
.green {
    color:	#3CB371;
}


